@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700&display=swap);
.logo-container{width:100%;max-width:1120px;height:100vh;margin:0 auto;display:flex;align-items:center;justify-content:space-between}.logo-container section.form{width:100%;max-width:350px}.logo-container section.form form{margin-top:100px}.logo-container section.form form h1{font-size:32px;margin-bottom:32px}.register-container{width:100%;max-width:1120px;height:100vh;margin:0 auto;display:flex;align-items:center;justify-content:center}.register-container .content{width:100%;padding:96px;background:#f0f0f5;box-shadow:0 0 10px rgba(0,0,0,0.1);border-radius:8px;display:flex;justify-content:space-between;align-items:center}.register-container .content section{width:100%;max-width:380px}.register-container .content section h1{margin:64px 0 32px;font-size:32px}.register-container .content section p{font-size:18px;color:#737380;line-height:32px}.register-container .content form{width:100%;max-width:450px}.register-container .content form input{margin-top:10px}.register-container .content form .input-group{display:flex}.register-container .content form .input-group input+input{width:80px;margin-left:8px}.register-container .content form .input-group input:first-child{width:auto;min-width:360px}

.profile-container{width:100%;max-width:1180px;padding:0 30px;margin:32px auto}.profile-container h1{margin-top:80px;margin-bottom:24px}.profile-container ul{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:24px;list-style:none}.profile-container ul li{background:#FFF;padding:24px;border-radius:8px;position:relative}.profile-container ul li button{position:absolute;right:24px;top:24px;border:0;background:transparent}.profile-container ul li button:hover{opacity:0.8}.profile-container ul li strong{display:block;margin-bottom:16px;color:#41414d}.profile-container ul li p+strong{margin-top:32px}.profile-container ul li p{color:#737380;line-height:21px;font-size:16px}.profile-container header{display:flex;align-items:center}.profile-container header span{font-size:20px;margin-left:24px}.profile-container header img{height:64px}.profile-container header a{width:260px;margin-left:auto;margin-top:0}.profile-container header button{height:60px;width:60px;border-radius:4px;border:1px solid #dcdce6;background:transparent;margin-left:16px;transition:border-color 0.2s ease-in-out}.profile-container header button:hover{border-color:#999}.new-incident-container{width:100%;max-width:1120px;height:100vh;margin:0 auto;display:flex;align-items:center;justify-content:center}.new-incident-container .content{width:100%;padding:96px;background:#f0f0f5;box-shadow:0 0 10px rgba(0,0,0,0.1);border-radius:8px;display:flex;justify-content:space-between;align-items:center}.new-incident-container .content section{width:100%;max-width:380px}.new-incident-container .content section h1{margin:64px 0 32px;font-size:32px}.new-incident-container .content section p{font-size:18px;color:#737380;line-height:32px}.new-incident-container .content form{width:100%;max-width:450px}.new-incident-container .content form input,.new-incident-container .content form textarea{margin-top:10px;resize:vertical}

*{box-sizing:border-box;margin:0;padding:0;outline:0}body{font:400 14px 'Montserrat', sans-serif;background:#f0f0f5;-webkit-font-smoothing:antialiased}input,button,textarea{font:400 18px 'Montserrat', sans-serif}button{cursor:pointer}form input{width:100%;height:60px;color:#333;border:1px solid #dcdce6;border-radius:8px;padding:0 24px}form textarea{width:100%;height:60px;min-height:140px;color:#333;border:1px solid #dcdce6;border-radius:8px;padding:16px 24px;line-height:24px}.button{width:100%;height:60px;background:#e02041;border:0;border-radius:8px;color:#FFF;font-weight:700;margin-top:16px;display:inline-block;text-align:center;text-decoration:none;font-size:18px;line-height:60px;transition:-webkit-filter 0.2s ease-in-out;transition:filter 0.2s ease-in-out;transition:filter 0.2s ease-in-out, -webkit-filter 0.2s ease-in-out}.button:hover{-webkit-filter:brightness(80%);filter:brightness(80%)}.backl-ink{display:flex;align-items:center;margin-top:40px;color:#41414d;font-size:18px;text-decoration:none;font-weight:500;transition:opacity 0.2s ease-in-out}.backl-ink svg{margin-right:8px}.backl-ink:hover{opacity:0.8}

